:root{
    --margins:350px;
    --mainSize:20px;
}

    

*{box-sizing: border-box;
    font-family: "Museo Sans Cyrl";
    margin:0;
    padding:0;
    }
    img{max-width: 100%;}
    section{min-height: 100vh;position:sticky;top: 90px;}
    body{margin:0px;}
    #wrapper{position:sticky;top:90px;z-index:111}
    #navbar{
        width:100%;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top:0px;
    z-index: 111;
    padding:40px var(--margins) 20px var(--margins);
    font-size: 20px;
    a{text-decoration: none;color:rgb(136 136 136);}
    a:hover{color:black;}
    }
    
    .nav-left{
        width:80%;
    display: flex;
    justify-content:flex-start;
    }
    .lang{display: flex;cursor: pointer;}
    .nav{margin-right:29px;}
    
    .line{max-width:100%;height:1px;background: black;margin:85px var(--margins) 0px var(--margins);}

    /////home/////
    #home{
        padding: 32px var(--margins) 65px var(--margins);
        width:100%;
    }
    .home-top{
        display: flex;
        justify-content: space-between;
        h1{font-size: 47px;}
        p{font-size: var(--mainSize);margin-top: 50px;}
        
    }
.main-img{margin: 50px 0px 65px 0px;}
//// END OF HOME////
///////ABOUT./////
#about{
    background: rgb(246 246 246);
 padding:296px 641px 205px 641px;
 text-align: center; 
 font-size: var(--mainSize);
}
.about-me{
    font-size: 30px;
}
.p{margin-top: 30px;line-height: 24px;}